// # Pillbox Control
// ### Core

// Bring in the [shared library functions](../lib/lib).
import * as Lib from '../lib/lib';

// Inherit from the [base control](base).
import Base from './base';

// Traits
import Disableable from '../traits/disableable';
import Multiselectable from '../traits/multiselectable';

export const CONTROL = 'Pillbox';

const PillboxCore = Lib.merge({}, Base, Disableable, Multiselectable, {
	CONTROL,
	
	// CSS classes used within this control
	cssClasses: {
		CONTROL: 'slds-pill'
	},

	_defaultProperties: {
		multiSelect: true,
		selection: [],
		acceptKeyCodes: [13, 188]
	},

	accessors: {
		getText (item) {
			return item.get('text');
		},

		getValue (item) {
			return item.get('value');
		}
	},

	_canSelect (item, select) {
		if (Lib.isFunction(this._onAdd)) {
			Promise.resolve(this._onAdd(item)).then(canSelect => {
				if (canSelect !== false) {
					select();
				}
			}, error => {
				Lib.log(error);
			});
		} else {
			select();
		}
	},

	_canDeselect (item, deselect) {
		if (Lib.isFunction(this._onRemove)) {
			Promise.resolve(this._onRemove(item)).then(canDeselect => {
				if (canDeselect !== false) {
					deselect();
				}
			}, error => {
				Lib.log(error);
			});
		} else {
			deselect();
		}
	},

	_isAcceptKeyCode (keyCode) {
		const acceptKeys = this.getProperty('acceptKeyCodes');
		let isAccepted;

		acceptKeys.forEach( (key) => {
			if (key === keyCode) {
				isAccepted = true;
			}
		});

		return isAccepted;
	}
});

export default PillboxCore;



